/* header main footer */
.wrapperA {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.wrapperHT {
    flex-basis: auto;
    flex-grow: 1;
    flex-shrink: 1;
}

.page {
    display: flex;
    justify-content: center;
    margin: 70px 15px 30px 15px;
}

/*Header*/
.page__section {
    padding: 30px;
    max-width: 1200px;
    margin: 0 auto;
}

.page__section_1 {
    background-color: #4e9bba;
}

.page__section_2 {
    background-color: #526454;
}

.page__section_3 {
    background-color: #c19492;
}

.page__title {
    font-size: 40px;
    margin: 0px 0px 20px 0px;
}

.page__text {
    font-size: 16px;
    line-height: 150%;
}

.page__text p {
    margin: 0px 0px 20px 0px;
}

.page__sub-title {
    font-size: 30px;
    margin: 0px 0px 20px 0px;
}

/*Header*/

.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    background-color: #4170a9;
}

.header__container {
    max-width: 1200px;
    margin: 0px auto;
    display: flex;
    padding: 0px 30px;
    align-items: center;
    justify-content: left;
    min-height: 70px;
}

.header__logo {
    border-radius: 50%;
    background-color: #fff;
    position: relative;
    z-index: 5;
    width: 40px;
    height: 40px;
}

.header__menu {
}

.menu {
}

.menu__icon {
    z-index: 5;
    display: block;
    position: relative;
    width: 30px;
    height: 18px;
    cursor: pointer;
}

.menu__icon span,
.menu__icon::before,
.menu__icon::after {
    left: 0;
    position: absolute;
    height: 10%;
    width: 100%;
    transition: all 0.3s ease 0s;
    background-color: #fff;
}

.menu__icon::before,
.menu__icon::after {
    content: ""
}

.menu__icon::before {
    top: 0;
}

.menu__icon::after {
    bottom: 0;
}

.menu__icon span {
    top: 50%;
    transform: scale(1) translate(0px, -50%);
}

.menu__icon._active span {
    transform: scale(0) translate(0px, -50%);
}

.menu__icon._active::before {
    top: 50%;
    transform: rotate(-45deg) translate(0px, -50%);
}

.menu__icon._active::after {
    bottom: 50%;
    transform: rotate(45deg) translate(0px, 50%);
}

/* ---------------------------------------------------- */

.menu__body {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: #c9e6ff;
    padding: 100px 30px 30px 30px;
    transition: left 0.3s ease 0s;
    overflow: auto;
}

.menu__body._active {
    left: 0;
}

.menu__body::before {
    content: "";
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    height: 70px;
    background-color: #4170a9;
    z-index: 2;
}

.menu__link:hover {
    text-decoration: underline;
}

.menu__link {
    color: #fff;
    display: block;
    margin: 21px 20px 21px 0;
    font-size: 24px;
}

.menu__sub-link {
    font-size: 20px;
    color: #000;
}

.menu__sub-link {
    color: #fff;
}

.menu__sub-link:hover {
    text-decoration: underline;
}



/*------------------*/
body._pc .menu__list > li:hover .menu__sub-list {
    opacity: 1;
    visibility: visible;
    transform: translate(0px, 0px);
    pointer-events: all;
}

body._touch .menu__list > li {
    display: flex;
    align-items: center;
}

body._touch .menu__link {
    flex: 1 1 auto;
    color: #444444;
}

body._touch .menu__arrow {
    display: block;
    width: 0;
    height: 0;
    margin: 0px 0px 0px 5px;
    transition: transform 0.3s ease 0s;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-top: 10px solid #fff;
}

body._touch .menu__list > li._active .menu__sub-list {
    opacity: 1;
    visibility: visible;
    transform: translate(0px, 0px);
    pointer-events: all;
}

body._touch .menu__list > li._active .menu__arrow {
    transform: rotate(-180deg)
}

/* Footer height */
.footer {
    min-height: 70px;
}

